import React, {useEffect, useState, useContext} from 'react';
import {UserContext} from '../../StoryApp';

const MyProfile = () =>{
    const [mypics, setpics] = useState([]);
    const [state, dispatch] = useContext(UserContext);
    const [image,setImage] = useState("");

    //console.log(state);
    useEffect(()=> {
         fetch('/myposts', {
             headers: {
                 "Authorization" : "Federer " + localStorage.getItem("jwt")
             }
         }).then(res=>res.json())
         .then(result =>{
             console.log(result);
             setpics(result.myposts)
         })
    }, [])

    
    useEffect(()=>{
        if(image){
         const data = new FormData()
         data.append("file", image);
         data.append("upload_preset","story_tales")
         data.append("cloud_name","dsicoouo4")
         fetch("https://api.cloudinary.com/v1_1/dsicoouo4/image/upload",{
             method:"post",
             body:data
         })
         .then(res=>res.json())
         .then(data=>{
     
        
            fetch('/updatepic',{
                method:"put",
                headers:{
                    "Content-Type":"application/json",
                    "Authorization":"Federer "+localStorage.getItem("jwt")
                },
                body:JSON.stringify({
                    pic:data.url
                })
            }).then(res=>res.json())
            .then(result=>{
                console.log(result)
                localStorage.setItem("user",JSON.stringify({...state,pic:result.pic}))
                dispatch({type:"UPDATEPIC",payload:result.pic})
                //window.location.reload()
            })
        
         })
         .catch(err=>{
             console.log(err)
         })
        }
     },[image])
     const updatePhoto = (file)=>{
         setImage(file)
     }
 
 

    return(
        <div style={{maxWidth: "550px", margin: "0px auto"}}>
            <div style={{
                //display: "flex",
               // justifyContent:"space-around",
                margin:"18px 0px",
                borderBottom:"1px solid black"
            }}>

<div style={{
               display:"flex",
               justifyContent:"space-around",
              
           }}>
                <div>
                    <img style ={{width:"178px", height:"160px", borderRadius:"80px"}}
                    src ={state?state.pic:"loading"}
                    alt ="person"
                    />
                </div>
                <div>
                    <h4>{state?state.name:"loading"}</h4>
                    <h5>{state?state.email:"loading"}</h5>
                <div style={{
                    display:"flex", justifyContent:"space-around", width:"130%"
                }}>
                

                 <h6>{mypics.length} Posts</h6>
                       <h6>{state?state.followers.length:"0"} Followers</h6>
                       <h6>{state?state.following.length:"0"} Following</h6>
                </div>
                 </div>
               </div>

                 <div className="file-field input-field" style={{margin:"10px"}}>
            <div className="btn #64b5f6 blue darken-1">
                <span>Update pic</span>
                <input type="file" onChange={(e)=>updatePhoto(e.target.files[0])} />
            </div>

            <div className="file-path-wrapper">
                <input className="file-path validate" type="text" />
            </div>
            </div>
            </div>

                   <div className="galery">
                       {
                           mypics.map(item => {
                               return (
                                <img key={item._id} className="items"
                                src ={item.photo} 
                                alt ={item.title} />
                               )
                           })
                       }
                   
                
                   </div>
        </div>
    );
}

export default MyProfile;